<template>
  <div>
    <b-modal
      id="modal_formulario_vista_lectura"
      ok-only
      size="lg"
      class="p-5"
      title="RESPUESTAS FORMULARIO"
      no-close-on-esc
      hide-footer
      modal-class="modal-primary"
    >
      <h3 class="text-center text-primary">
        <strong>{{ formulario.nombre }}</strong>
      </h3>
      <!-- <div>

        <br>
        {{ formulario.descripcion }}

      </div> -->

      <form-wizard
        next-button-text="Siguiente"
        back-button-text="Anterior"
        color="#06634c"
        :title="null"
        :subtitle="null"
        layout="horizontal"
        :finish-button-text="'Fin'"
        class="wizard-horizontal"
        style="background:#F4F4F4"
      >
        <tab-content
          v-for="(seccion, index) in formulario.secciones"
          :key="index"
          class="px-0 py-0"
        >
          <b-card
            class="bg-light-primary m-0"
          >
            <span style="font-size:16px"><strong>{{ seccion.titulo }}</strong></span><br>
            <span
              class="ml-2"
              style="font-size:13px"
            >{{ seccion.subtitulo }}</span>
          </b-card>
          <!-- <div v-if="seccion.condicional === 1">
            <b-card class="py-1 mt-1">
              <b-form-checkbox
                v-model="respuestaCondicional[index].value"
                switch
                :value="false"
                :unchecked-value="true"
                class="custom-control-primary"
                :disabled="estadoFormulario"
              >
                {{ seccion.condicional_texto }}
              </b-form-checkbox>
            </b-card>
          </div> -->
          <!-- <div v-if="respuestaCondicional[index].value === true"> -->
          <respuestas
            class="mt-1"
            :preguntas="seccion.preguntas"
          />
          <!-- </div> -->
        </tab-content>
      </form-wizard>
    </b-modal>
  </div>
</template>

<script>

import {
  BModal, BCard,
// BRow, BCard, BCol, BButton,
} from 'bootstrap-vue'
import { FormWizard, TabContent } from 'vue-form-wizard'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import Respuestas from './Respuestas.vue'

// import { mapState } from 'vuex'
// import { onUnmounted } from '@vue/composition-api'
// import formularioStoreModule from '@/views/administrador/gestion-formulario/formulario/formularioStoreModule'
// import secciones from '@/views/administrador/formulario/Secciones.vue'
// import BtnEnviarFormulario from './BtnEnviarFormulario.vue'

export default {
  components: {
    BModal,
    FormWizard,
    TabContent,
    Respuestas,
    BCard,
    // BCol,
    // BButton,
    // // externos
    // secciones,
    // BtnEnviarFormulario,
  },
  props: {
    idFormulario: {
      type: Number,
      default: 0,
    },
    idUsuario: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      formulario: {},
      // formularioRespondido: false,
    }
  },
  computed: {
    // ...mapState('formularioModule', ['respuestaCondicional', 'estadoFormulario']),

  },
  created() {
    this.$parent.$on('abrirModalLecturaFichaClinica', this.abrirModalLecturaFichaClinica)
  },
  mounted() {

  },

  methods: {
    abrirModalLecturaFichaClinica() {
      this.cargarRespuesta()
      this.$bvModal.show('modal_formulario_vista_lectura')
    },
    cargarRespuesta() {
      this.$http.get(`/recursos/formulario/${this.idFormulario}/respuesta?usuario_id=${500}`).then(res => {
        console.log('DATA: ', res)
        this.formulario = res.data
      })
    },

  },
}
</script>
<style lang="scss">
  @import '@core/scss/vue/libs/vue-wizard.scss';
  </style>
<style scoped>
 .wizard-icon-container{
   background-color: red;
 }
</style>
